import {useState, useEffect} from 'react' 
import styled from 'styled-components'
import arrowTop from '../img/arrow_top.png';

const ButtonWrap = styled.div`
  position:fixed; right:10px; bottom:56px; 
`;
const ScrollTop = styled.button`

transition:all 0.4s;
position:fixed; bottom: 56px; right:10px; 
background: ${(props) => props.theme.topBtnBg};
box-shadow: ${(props) => props.theme.topBtnBoxShadow};
border-radius: 12px;
width:40px; height:40px; border:none; outline:none;
padding:12px 14px 13px;
opacity:0;
&.active{opacity:1;}
img{max-width:100%;}
`;
export const TopButton = ({scrollY})=> {
  const [BtnStatus, setBtnStatus] = useState(false); 
  const wrapperDiv = document.querySelector('.wrapper');
  // const [ScrollY, setScrollY] = useState(0); // 
  // const [BtnStatus, setBtnStatus] = useState(false); //  
  // let scrollY = 0;
  //    
   const handleFollow = () => {
    if(scrollY > 100) {
      // 100   
      setBtnStatus(true);
    } else {
      // 100   
      setBtnStatus(false);
    }
  }


  const handleTop = () => {  //     
    wrapperDiv.scroll({
      top:0, 
      behavior:'smooth'
    });
    // wrapperDiv.scrollTop = 0;
    // scrollY = 0;  // ScrollY   
    setBtnStatus(false); // BtnStatus  false  =>  
  }

  useEffect(() => {
    const watch = () => {
      handleFollow()
    }
    watch();
  },[scrollY])


  return <ButtonWrap>
  <ScrollTop onClick={handleTop} className={BtnStatus ? "topBtn active" : "topBtn"}><img src={arrowTop} alt=""/></ScrollTop>
  
  </ButtonWrap>

}